export class Particle {
    constructor() {
        this.position = { x: 0, y: 0 };
        this.velocity = { x: 0, y: 0 };
        this.color = { r: 1, g: 1, b: 1 };
        this.alpha = 1.0;
        this.rotation = 0;
        this.rotationalVelocity = 0;
        this.timeLived = 0;
        this.lifeTime = 2;
        this.scale = 0.1;
        this.destroyed = false;
    }
}
